@import url(https://fonts.googleapis.com/css?family=Jost|Lato|Noto+Sans+JP|Open+Sans|Roboto|Roboto+Condensed|Roboto+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
	text-align: center;
	margin-top: 35px;

	font-family: 'Roboto', sans-serif;
}

body {
	text-align: center;
	background: #100e0f;
	background-image: url(/static/media/black-hand.186deae5.jpg);
	background-repeat: no-repeat;
	background-attachment: fixed;
	background-position: center;

	font-weight: 100;
}
.header {
	display: flex;
	justify-content: center;
	align-items: center;
}
h1 {
	color: #f2f2f2;
	font-weight: 600;
	font-size: 40px;
	margin: 40px 0px 20px;

	width: 10vw;
	line-height: 32px;
}

#clockdiv {
	font-family: sans-serif;
	color: #2a363b;
	display: inline-block;
	font-weight: 100;
	text-align: center;
	font-size: 30px;
}

#clockdiv > div {
	padding: 10px;
	border-radius: 3px;
	background: #333333;
	display: inline-block;
}

#clockdiv div > span {
	padding: 15px;
	border-radius: 3px;
	background: #f2f2f2;
	display: inline-block;
}

.smalltext {
	padding-top: 5px;
	font-size: 16px;
	color: white;
}

.img-container {
	padding: 20px;
}

img {
	height: 100px;
	width: 100px;
}

